.checkbox {
  position: relative;
  margin-top: auto;
  margin-bottom: auto;
  padding-left: 52px;
}

.checkbox__label {
  font-size: 16px;
  line-height: 24px;
}

.checkbox__mark {
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 42px;
  height: 24px;
  background-color: #bdbdbd;
  border: 2px solid #bdbdbd;
  border-radius: 12px;
}
.checkbox__mark::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border-radius: 50%;
  content: "";
}

.checkbox__input:hover + .checkbox__mark {
  border-color: #9070ec;
  background-color: rgba(120, 89, 207, 0.2509803922);
}
.checkbox__input:checked + .checkbox__mark {
  border-color: #7859cf;
  background-color: #7859cf;
  transition: all 1s ease-out;
}
.checkbox__input:checked + .checkbox__mark::after {
  right: 0;
  left: unset;
}
.checkbox__input:checked:disabled + .checkbox__mark::after {
  background-color: #bdbdbd;
}
.checkbox__input:disabled + .checkbox__mark {
  border-color: #e2e2e2;
  background-color: #e2e2e2;
}
.checkbox__input:disabled ~ .checkbox__label {
  color: #e2e2e2;
}

