  }
}

@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__list--site {
    display: none;
  }
}

.main-navigation--opened .main-navigation__list--site {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 10;
}

.main-navigation__toggle {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 60px;
  height: 60px;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  border: none;
  z-index: 20;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-navigation__toggle:active {
    background-color: #f0ebff;
  }
}

.main-navigation--closed .main-navigation__toggle-icon {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  position: inherit;
  display: block;
  width: 16px;
  height: 2px;
  background-color: #333333;
  transition: 0.3s;
}
.main-navigation--closed .main-navigation__toggle-icon::before, .main-navigation--closed .main-navigation__toggle-icon::after {
  position: absolute;
  right: 0;
  width: 16px;
  height: 2px;
  background-color: #333333;
  content: "";
}
.main-navigation--closed .main-navigation__toggle-icon::before {
  top: -5px;
  transition: top 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
.main-navigation--closed .main-navigation__toggle-icon::after {
  bottom: -5px;
  transition: bottom 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.main-navigation--opened .main-navigation__toggle-icon {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  position: inherit;
  width: 20px;
  height: 2px;
  transition: 0.3s;
}
.main-navigation--opened .main-navigation__toggle-icon::before, .main-navigation--opened .main-navigation__toggle-icon::after {
  position: absolute;
  right: 2px;
  width: 16px;
  height: 2px;
  content: "";
  background-color: #333333;
}
.main-navigation--opened .main-navigation__toggle-icon::before {
  top: 0;
  transform: rotate(45deg);
  transition: top 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
}
.main-navigation--opened .main-navigation__toggle-icon::after {
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.main-navigation--nojs .main-navigation__toggle {
  display: none;
}

